import React, { useEffect } from 'react';
import { Form, Card, Button } from 'react-bootstrap';
import { Chart as ChartJS, registerables } from 'chart.js';
import { Tooltip } from 'chart.js';
import { Chart } from 'react-chartjs-2'
import 'chartjs-adapter-moment';
import annotationPlugin from 'chartjs-plugin-annotation';
import zoomPlugin from 'chartjs-plugin-zoom';
import { GNSS_INFO, GNSS_INFO_LIST, PARAMS } from '../../Pages/Dashboard/Dashboard';


ChartJS.register(...registerables);
ChartJS.register(zoomPlugin);
ChartJS.register(annotationPlugin)

var _ = require('lodash');

function Chart2(props) {

    const data = {
        labels: _.map(props.data['GP'], e => e.time),
        datasets: GNSS_INFO_LIST.map((gnss, i) => ({
            label: gnss.name,
            data: _.map(props.data[gnss.label], e => e[props.mode.parameter]),
            pointRadius: 0,
            lineTension: 0.25,
            borderColor: gnss.color,
            backgroundColor: gnss.color_lighter,
            borderWidth: 2,
        })),
    }

    return (
        <div style={{ padding: '0px', height: '100%', width: '100%', position: 'relative' }}>
            <Chart
                type={'line'}
                data={data}
                options={{
                    // parsing: false,
                    plugins: {
                        zoom: {
                            pan: {
                                enabled: true,
                                modifierKey: 'ctrl',
                            },
                            zoom: {
                                drag: {
                                    enabled: true,
                                },
                                mode: 'xy',
                            },
                        },
                        // decimation: {
                        //     enabled: true,
                        //     algorithm: 'lttb'
                        // },
                        annotation: {
                            annotations: {
                                line: {
                                    type: 'line',
                                    yMin: props.exact,
                                    yMax: props.exact,
                                    borderColor: 'rgba(0,0,0,0.8)',
                                    borderWidth: 2,
                                    borderDash: [10, 5],
                                    label: {
                                        content: `Exact value: ${PARAMS[props.mode.parameter].display(props.exact)}`,
                                        enabled: true,
                                        backgroundColor: 'rgba(0,0,0,0)',
                                        color: 'black',
                                        position: 'end',
                                        yAdjust: 12
                                    }
                                }
                            }
                        },
                        tooltip: {
                            // position: 'customPositioner',
                            callbacks: {
                                label: (context) => {
                                    return context.dataset.label + ': ' + PARAMS[props.mode.parameter].display(context.parsed.y)
                                }
                            }
                        }
                    },
                    interaction: {
                        intersect: false,
                        mode: 'index'
                    },
                    responsive: true,
                    maintainAspectRatio: false,
                    animation: false,
                    scales: {
                        x: {
                            display: false,
                            type: 'timeseries',
                            ticks: {
                                maxTicksLimit: 5,
                                maxRotation: 0,
                            }
                        },
                        y: {
                            ticks: {
                                callback: (value) => PARAMS[props.mode.parameter].display(value),
                                maxTicksLimit: 8
                            }
                        }
                    }
                }}
            />
        </div>
    )
}

export default Chart2