import './ChartWidget.scss';
import React from 'react';
import Widget from '../Widget';
import { Nav, Button, Form } from 'react-bootstrap';
import Chart from './Chart';
import { queryAllByAttribute } from '@testing-library/react';
import Chart2 from './Chart2';

const intervals = [
    { val: 5, label: '5M' },
    { val: 30, label: '30M' },
    { val: 60, label: '1H' },
    { val: 300, label: '5H' },
    { val: 1440, label: '1D' },
]

const widths = {
    1: [100],
    2: [100, 100],
    3: [100, 50, 50],
    4: [50, 50, 50, 50]
}


const modes = [
    // 'Dillution Of Precision': {
    //     names: ['Vertical DOP', 'Horizontal DOP', 'Positional DOP'],
    //     parameters: ['vdop', 'hdop', 'pdop'],
    //     unit: ''
    // },
    {
        name: 'Latitude',
        parameter: 'lat',
        unit: 'N',
    },
    {
        name: 'Longitude',
        parameter: 'lon',
        unit: 'E',
    },
    {
        name: 'Altitude',
        parameter: 'alt',
        unit: 'm',
    },
    {
        name: 'Speed',
        parameter: 'speed',
        unit: 'm/s',
        actual: 0
    }
]

class ChartWidget extends React.Component {

    constructor(props) {
        super(props);

        this.state = {
            charts: 1,
            modeIndex: props.mode
        }
    }

    toggleDual = () => {
        this.setState({
            dual: !this.state.dual
        })
    }

    charts(q) {
        return (
            [1, 2, 3, 4].map(e =>
                <Chart2
                    key={`chart_${e}`}
                    height={q > 1 ? '50%' : '100%'}
                    width={`calc(${widths[q][e - 1]}% - 2px)`}
                    style={{ float: 'left', display: e > this.state.charts ? 'none' : '' }}
                    handleIntervalChange={this.handleIntervalChange}
                    data={this.props.data}
                />
            )
        )
    }

    mode() {
        return modes[this.state.modeIndex]
    }

    exactValue = () => this.props.exactValues[this.mode().parameter]

    render() {
        return <Widget
            widgetKey={this.props.widgetKey}
            removeItem={this.props.removeItem}
            // loaing={false}
            loading={this.props.loading}
            title={
                <div className="ChartWidget" style={{ height: '31px', display: 'flex', alignItems: 'center', justifyContent: 'space-between' }}>
                    <div>
                        Time Series ({this.mode().name})
                    </div>

                    {/* <Nav
                        style={{ width: '100%' }}
                        activeKey={this.props.intervalMinutes}
                        onSelect={(value) => this.props.handleIntervalChange(value)}
                        className="justify-content-center"
                    >
                        {
                            intervals.map(i =>
                                <Nav.Item key={`interval_select_${i.val}`}>
                                    <Nav.Link style={{ paddingTop: '5px' }} eventKey={i.val}>{i.label}</Nav.Link>
                                </Nav.Item>
                            )
                        }
                    </Nav> */}
                    <Form.Select
                        size='sm'
                        onChange={e => { this.setState({ modeIndex: e.target.value }); this.forceUpdate(); }}
                        style={{
                            fontSize: '14px',
                            width: '110px',
                            // position: 'absolute',
                            // right: '16px',
                            // top: '21px',
                            zIndex: 3
                        }}
                        value={this.state.modeIndex}
                    >
                        {
                            modes.map((m, i) =>
                                <option
                                    key={`select_charts_${m.parameter}`}
                                    value={i}
                                >{m.name}</option>
                            )
                        }
                    </Form.Select>
                </div>
            } style={{
                width: this.props.width ? this.props.width : '100%',
                height: this.props.height ? this.props.height : '100%',
                float: 'left'
            }}>
            {/* 
            {
                this.charts(this.state.charts)
            } */}

            <Chart2
                // key={`chart_${e}`}
                height={'100%'}
                width={`calc(100% - 2px)`}
                style={{ float: 'left' }}
                handleIntervalChange={this.handleIntervalChange}
                data={this.props.data}
                mode={this.mode()}
                exact={this.exactValue()}
            />

            {/* <Chart height={'100%'}
                width={`calc(100% - 2px)`}
                style={{ float: 'left' }}
                handleIntervalChange={this.handleIntervalChange}
                data={this.props.data}
                mode={this.mode()}
                exact={this.exactValue()}
            /> */}

        </Widget>
    }
}

export default ChartWidget;