import React from 'react';
import { GNSS_INFO } from '../../Pages/Dashboard/Dashboard';
import Widget from '../Widget';

const radarDimension = 150

class RadarWidget extends React.Component {

    elevationToRadius = (e) => (90 - e) / 90
    computeX = (e, a) => this.elevationToRadius(e) * Math.sin((180 - a) * (Math.PI / 180))
    computeY = (e, a) => this.elevationToRadius(e) * Math.cos((180 - a) * (Math.PI / 180))

    ref = React.createRef()

    getDiameter() {
        return this.ref.current ? (Math.min(this.ref.current.offsetHeight, this.ref.current.offsetWidth) * 0.8) / 2 : 0
    }

    icon(sat) {
        return <div
            className='clickable'
            key={`${sat.gnss}_${sat.prn}`}
            style={{
                borderRadius: '50%',
                width: '22px',
                height: '22px',
                textAlign: 'center',
                backgroundColor: GNSS_INFO[sat.gnss].color,
                padding: '0px',
                // border: `2px solid ${GNSS_INFO[sat.gnss].color_lighter}`,
                // outline: `2px solid ${sat.active ? 'darkblue' : GNSS_INFO[sat.gnss].color}`,
                borderStyle:'solid',
                borderWidth: '2px',
                borderColor: sat.active ? 'darkblue' : GNSS_INFO[sat.gnss].color,
                color: 'black',
                fontSize: '12px',
                // transform: `translate(${radarDimension - 10}px, ${radarDimension - 10}px)`,
                position: 'absolute',
                // left: `${this.computeX(sat.elevation, sat.azimuth) * radarDimension}px`,
                left: `calc(50% - 11px + ${this.computeX(sat.elevation, sat.azimuth) * this.getDiameter()}px)`,
                // top: `${this.computeY(sat.elevation, sat.azimuth) * radarDimension}px`,
                top: `calc(50% - 11px + ${this.computeY(sat.elevation, sat.azimuth) * this.getDiameter()}px)`,
                // transform: `translate(50%, 50%)`,
                transition: 'cubic-bezier(0.250, 0.460, 0.450, 0.940) 0.3s',
                zIndex: 100
            }}>
            <div style={{ width: '100%', height: '100%', position: 'relative', display: 'flex', alignItems: 'center' }}>
                <span style={{ position: 'absolute', left: '0px', right: '0px' }}>
                    {sat.prn}
                </span>
            </div>
        </div>
    }

    render() {
        // let satellites = this.props.navStates.reduce(
        //     (prev, curr) => [...prev, ...curr.satsVisible], []
        // ).filter(s => s.azimuth)

        // satellites = [
        //     { prn: 5, gnss: 'GP', active: false, azimuth: 90, elevation: 0 }
        // ]

        return <Widget 
        widgetKey={this.props.widgetKey}
        removeItem={this.props.removeItem}
        style={{
            width: this.props.width ? this.props.width : '100%',
            height: this.props.height ? this.props.height : '100%',
            float: 'left'
        }} title="Satellite Radar">
            <div
                ref={this.ref}
                // className='square'
                style={{
                    width: '100%',
                    height: '100%',
                    // display: 'flex',
                    // justifyContent: 'center',
                    // alignItems: 'center',
                    position: 'relative',
                    // paddingBottom:'auto',
                    // paddingLeft: 'auto'
                }}>

                <img src='protractor_3.svg' width='100%' height='100%' style={{ position: 'absolute' }} />
                {
                    this.props.navStates.reduce(
                        (prev, curr) => [...prev, ...curr.satsVisible], []
                    ).filter(s => s.azimuth).map(sat => this.icon(sat))
                }
                {/* <div style={{
                    width: `${radarDimension * 2}px`,
                    height: `${radarDimension * 2}px`,
                    backgroundColor: '#f5f9ff',
                    borderStyle: 'solid',
                    borderWidth: '1px',
                    borderRadius: '50%',
                    position: 'relative',
                    zIndex: 5
                }}>
                    <div style={{
                        width: `${radarDimension * 4 / 3}px`,
                        height: `${radarDimension * 4 / 3}px`,
                        borderStyle: 'solid',
                        borderWidth: '1px',
                        backgroundColor: '#ebf0f7',
                        // opacity: 0.15,
                        borderColor: '#aeb5bf',
                        position: 'absolute',
                        borderRadius: '50%',
                        transform: `translate(${(radarDimension / 3) - 2}px, ${(radarDimension / 3) - 2}px)`,
                        zIndex: 6
                    }} />

                    <div style={{
                        width: `${radarDimension * 2 / 3}px`,
                        height: `${radarDimension * 2 / 3}px`,
                        borderStyle: 'solid',
                        borderWidth: '1px',
                        backgroundColor: '#f5f9ff',
                        // opacity: 0.15,
                        borderColor: '#aeb5bf',
                        position: 'absolute',
                        borderRadius: '50%',
                        transform: `translate(${(radarDimension / 3 * 2) - 2}px, ${(radarDimension / 3 * 2) - 2}px)`,
                        zIndex: 6
                    }} />

                    <div id="center" style={{
                        width: '4px',
                        height: '4px',
                        borderRadius: '50%',
                        backgroundColor: 'black',
                        transform: `translate(${radarDimension - 2}px, ${radarDimension - 2}px)`,
                        position: 'absolute',
                        zIndex: 7
                    }} />

                    {
                        satellites.map(sat => this.icon(sat))
                    }
                </div> */}

            </div>
        </Widget>
    }
}

export default RadarWidget;